}

.dropdown-caret {
  border-top-style: solid;
  border-top-width: 4px;
  border-right: 4px solid transparent;
  border-bottom: 0 solid transparent;
  border-left: 4px solid transparent;
}

.dropdown {
  position: absolute;
  top: 8vh;
  right: 1vh;
  border: 0.5px solid;
  border-radius: 5px;
  border-color: #212121;
  padding: 0.5%;
  box-shadow: 0 0 10px #212121;
  background-color: black;
  z-index: 2;
}

.user-name {
  margin-bottom: 5%;
  padding: 2%;
  width: 180px;
  border-bottom: 1px solid #212121;
  color: #757575;
}

.user-name:hover {
  cursor: default;
}

.dropdown ul {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 180px;
}

.dropdown li {
  padding: 2%;
  width: 180px;
}

.dropdown li:hover {
  background-color: #424242;
  border-radius: 3px;
}

